  font-family: "Young Style", sans-serif;
  font-family: "Young Style Underlines", sans-serif;
  src: url("https://fonts.cdnfonts.com/css/young-style") format("woff2");
}
#app {
  display: flex;
  flex-direction: column;
}
#app #title {
  text-align: left;
  font-family: "Young Style", sans-serif;
  font-size: 50px;
  color: #4a3f35;
  padding-left: 100px;
}
#app #find-order {
  text-align: center;
}
#app #find-order h2 {
  font-family: "Courier New", Courier, monospace;
}
#app #find-order #order-finder {
  font-family: "Young Style", sans-serif;
}
#app #find-order #order-finder #order-id {
  width: 500px;
}
#app #find-order #order-finder #find-order-by-id {
  background-color: #bcaaa4;
  border: none;
  border-radius: 0.5em;
  padding: 5px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #fff7e6;
}
#app #find-order #order-finder #find-order-by-id:hover {
  background-color: #4a3f35;
  color: #bcaaa4;
}
#app #display-order {
  background-color: white;
  width: 80%;
  border: #bcaaa4 solid 3px;
  border-radius: 0.5em;
  align-self: center;
  margin: 10px;
  padding: 10px;
}
#app #display-order h2 {
  text-align: center;
  font-family: "Young Style", sans-serif;
  font-size: 20px;
}
#app #display-order p {
  margin-left: 30px;
  font-family: "Courier New", Courier, monospace;
}
#app #display-order #status, #app #display-order #date, #app #display-order #orderid {
  text-align: center;
  margin-left: -30px;
}
#app .photos {
  max-width: 500px;
  justify-content: center;
